import { useState, useEffect } from 'react';

const CounterExp = () => {
    const [count, setCount] = useState(0);
    const targetCount = 9500;
    const incrementTime = 50; // milliseconds

    useEffect(() => {
        // Función para incrementar el contador
        const incrementCounter = () => {
            if (count < targetCount) {
                setCount(prevCount => Math.min(prevCount + 1, targetCount));
            }
        };

        // Establecer el intervalo para incrementar el contador
        const interval = setInterval(incrementCounter, incrementTime);

        // Limpiar el intervalo cuando el componente se desmonta
        return () => clearInterval(interval);
    }, [count]); // Dependencia de 'count', el efecto se ejecutará cada vez que cambie

    return (
        <div className="absolute bottom-0 left-0 w-full flex justify-center items-center z-10">
            <div className="relative w-full h-40 flex items-center justify-center">
                {/* Gradiente con bordes ligeramente transparentes */}
                <div
                    className="absolute w-full h-full bg-gradient-to-t"
                    style={{
                        background: "linear-gradient(to top, transparent 5%, black 20%, black 80%, transparent 95%)",
                    }}
                ></div>
            </div>
            <div className="relative text-white px-2 text-6xl font-bold">
                {count.toLocaleString()}
            </div>
        </div>
    );
};

export default CounterExp;
